import React from 'react'
import '../styles/gameFrame.scss'


const Minesweeper = (props) => {

  let loaded = false

  const checkLoad = () => {
    console.log('loaded')
    loaded = true
  }

  return (
    <div className='game-hold'>
    {loaded ? <iframe onLoad={checkLoad} src="http://104.248.216.64:6776/" frameBorder="0"></iframe> : <p>Loading</p>}
    </div>
  )
}
export default Minesweeper

